var that;
import orderlist from '../../order/order.js';
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			navtitle: [
				{ id: -1, name: '全部' },
				{ id: 0, name: '未发货' },
				{ id: 1, name: '已发货' },
				{ id: 2, name: '已签收' },
				{ id: 3, name: '待评价' },
				{ id: 4, name: '售后' }
			],
			list: [],
			onlist: false,
			page: 1,
			onmore: false,
			access_token: '',
			modalName: '',
			subkdid:''
		};
	},
	methods: {
		hiddenModel(){
			that.modalName=""
		},
		showkdModel(e){
			that.subkdid=e.currentTarget.dataset.id;
			that.modalName="kuaidi";
		}, 
		subkd(){
			that.hiddenModel();
		},
		login() {
			that.$zax.yzlogin();
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			(this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60), that.getorder();
			that.page = 1;
			that.list = [];
		},  
		todzlist(e) {
			uni.navigateTo({
				url: '/pages/card/cardinfo/cardinfo?id=' + e.currentTarget.dataset.id
			});
		},
		getorder() {
			that.onlist = true;
			this.$request
				.request({
					url:  that.api.live.anchor_order,
					data: {
						type:that.TabCur,
						page:that.page
					}
				})
				.then(res => { 
					console.log(res);
					if (res.data.list.length > 0){
						let list = res.data.list;
						that.list = list;
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了！');
								that.nomore = true;
							}
						} else {
							that.onlist = false;
						}
						that.list = [];
					} 
			 });
		},
		tpinfo(e) {
			uni.navigateTo({
				url: '/pages/order/shopOrderinfo/shopOrderinfo?id=' + e.currentTarget.dataset.id
			});
		},
		//评价
		pingjia(e) {
			uni.navigateTo({
				url: '/pages/order/pingjia/pingjia?id=' + e.currentTarget.dataset.id
			});
		},
		// 第二种详情
		tpinfo2(e) {
			// 售后详情
			console.log(e.currentTarget.dataset.order_refund_id);
			if (e.currentTarget.dataset.order_refund_id) {
				uni.navigateTo({
					url: '/pages/order/shouhou/shouhouInfo/shouhouInfo?id=' + e.currentTarget.dataset.order_refund_id
				});
			} else {
				uni.navigateTo({
					url: '/pages/order/orderinfo2/orderinfo2?id=' + e.currentTarget.dataset.id
				});
			}
		},
		//取消订单
		orderRevoke: function(e) {
			var a = this;
			that.tanchuang
				.showModel({
					title: '提示',
					content: '是否取消该订单',
					cancelText: '否',
					confirmText: '是'
				})
				.then(res => {
					if (res == 'confirm') {
						that.$request
							.get(
								{
									url: 'api/order/revoke',
									data: {
										order_id: e.currentTarget.dataset.id
									}
								},
								'正在执行'
							)
							.then(res => {
								if (res.code == 1) {
									that.$alert(res.msg);
								} else {
									console.log(res);
									that.$alert(res.msg);
									that.list = [];
									setTimeout(function() {
										that.getorder();
									}, 1000);
								}
							});
					}
				});
